import React from "react";
import { Outlet, Link } from "react-router-dom";

const Navbar = () => {
  return (
    <div className="hamburger-menu">
         
            <input id="menu__toggle" type="checkbox" />
    <label class="menu__btn" for="menu__toggle">
      <span></span>
    </label>
    
        
      <nav className="menu__box">
        <Link to="/">Головна</Link>
        <Link to="/theoryPage">Теоретичні відомості</Link>
        <Link to="/tagsPage">Теги</Link>
        <div className="dropdown">
          <Link style={{border:'none'}}  to="/semanticPage">Семантична верстка</Link>{" "}
          <i style={{ color: "#8C503A" }} className="fa fa-caret-down"></i>{" "}
          <div class="dropdown-content">
      <a href="semanticPage#semantic">Семантична верстка</a>
      <a href="semanticPage#section-article">&lt;section&gt; та &lt;article&gt;</a>
      <a href="semanticPage#aside">&lt;aside&gt;</a>
    </div>
        </div>
        <Link to="/aboutPage">Про нас</Link>
      </nav>
      <Outlet />
    </div>
  );
};

export default Navbar;
