import React from "react";

const Example = (props) => {
  return (
    <div>
      <details>
        <summary
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <span
            style={{
              backgroundColor: "#282C34",
              borderRadius: "10px",
              marginTop: "20px",
              padding: "5px 10px",
              fontFamily: `Consolas, 'Courier New', monospace`,
              color: "#266EF2",
            }}
          >
            Приклад

            <a target='blank' className="run-code-mobile" href={`https://www.tutorialrepublic.com/codelab.php?topic=html5&file=${props.tagName}-tag`}><i class="fa-solid fa-play"></i></a>
          </span>
        
        </summary>
        <pre className="code">
          <code dangerouslySetInnerHTML={{__html: props.exampleText}}>

          </code>
          <a target='blank' className="run-code" href={`https://www.tutorialrepublic.com/codelab.php?topic=html5&file=${props.tagName}-tag`}><i class="fa-solid fa-play"></i></a>
        </pre>
      </details>
    </div>
  );
};

export default Example;
