import React from "react";

import Navbar from "./navbar";
const Header = () => {
  return (
    <header
    className="header"
    id="top"
      style={{
        flexDirection: "row",
        justifyContent: "center"  ,
        borderBottom: '1px solid #8c503a'

      }}
    >
  <Navbar/>
    </header>
  );
};

export default Header;
