import React from "react";
import Header from "./header";
import Semantic from "../img/semantic.svg";
import Semantic2 from "../img/semantic2.svg";
import Semantic3 from "../img/semantic3.svg";
import Semantic4 from "../img/semantic4.svg";
import Footer from "./footer";

const SemanticPage = () => {
  return (
    <div>
      <Header />
      <main className="semantic-main">
        <h1>Семантична верстка</h1>
        <section id="semantic">
          <p>
            Семантичною називають верстку, що використовує для структурування
            html-документів теги, які поділяють код на логічні блоки (які явно
            показують їх роль і зміст у структурі web-сторінки). Цей вид верстки
            став доступний з появою HTML5, його нові елементи відіграють велику
            роль створення явної логічної структури документа. Для початку, щоб
            було зрозуміліше, як використовуються ці елементи, пропонуємо вам
            вивчити один із можливих макетів сторінки:
          </p>
          <img style={{ maxWidth: "100%" }} src={Semantic} alt="" />
          <p>
            Семантична верстка відрізняється від блокової лише елементами, що
            використовуються при структуризації сторінки, але на відміну від
            семантичних тегів, що використовуються в семантичній верстці, тег
            div, що є основним використовуваним елементом в блоковій верстці, не
            передає чіткий зміст про вміст, що міститься в ньому, так як він є
            лише контейнером для угруповання контенту.
          </p>
        </section>
        <section id="section-article">
          <h2>Використання &lt;section&gt; та &lt;article&gt; </h2>
          <p>
            Використання &lt;section&gt; та &lt;article&gt; є вже складнішим
            прикладом семантичної верстки. Тут з'являється ієрархія у головному
            контенті. Тег &lt;article&gt; охоплює та визначає основний контент
            усередині тега. Всередині &lt;article&gt; вкладено кілька розділів
            &lt;section&gt;, які є підтемами самої статті. Потрібно пам'ятати,
            що &lt;section&gt; формує частину чогось, а &lt;article&gt; існує
            сама по собі.
          </p>
          <img src={Semantic2} />
        </section>
        <section id="aside">
          <h2>Використання &lt;aside&gt;</h2>
          <p>
            Також у семантичній верстці досить часто використовують тег
            &lt;aside&gt;. У першому випадку контент у цьому тегу пов'язаний з
            основним контентом &lt;article&gt;. У ньому бажано розміщувати
            додатковий контент. Тобто стаття &lt;article&gt; має бути зрозуміла
            та без секції &lt;aside&gt;.
          </p>
          <img src={Semantic3} />
          <p>
            У другому випадку елемент &lt;aside&gt; побічно пов'язаний, тобто
            йому не обов'язково містити додаткову інформацію основного контенту.
            Він може бути застосований для блоків під основним контентом та
            містити в собі заголовок, текст, посилання на інші сторінки реклами.
          </p>
          <img src={Semantic4} />
       
        </section>
       
      </main>
      <Footer/>
    </div>
  );
};

export default SemanticPage;
