import React from "react";
import Example from "./example";

const Tag = (props) => {
  return (
    <div>
      <details
        style={{
          backgroundColor: "#024059",
          borderRadius: "10px",
          padding: "10px",
          color: "#d9ccc1",
          width:''
        }}
      >
        <summary
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            color: "#d9ccc1",
          }}
        >
          <span
            style={{
              backgroundColor: "#8c503a",
              borderRadius: "10px",
              padding: "10px",
              fontWeight: "800",
              fontSize: "18px",
            }}
          >
            &lt;{props.tagName}&gt;
          </span>
        </summary>
        <p style={{ textAlign: "justify" }}>{props.text}</p>
        <table
          border="1"
          style={{
            color: "#024059",
            backgroundColor: "#d9ccc1",
            width: "100%",
            borderSpacing: "0px",
            borderColor: "#024059",
            borderWidth: "0",
          }}
        >
          <tr>
            <th>Розміщення:</th>
            <td>{props.placement}</td>
          </tr>
          <tr>
            <th>Зміст:</th>
            <td>{props.content}</td>
          </tr>
          <tr>
            <th>Початковий/кінцевий тег:</th>
            <td>{props.startEndTag}</td>
          </tr>
        </table>
        <Example
          tagName={props.tagName}
        
          exampleText={props.exampleText}
        />
      </details>
    </div>
  );
};

export default Tag;
