import React from "react";
import Header from "./header";
import { Link } from "react-router-dom";
import Mark from "./mark";
import Speakers from "../img/speakers.svg";
import Painter from "../img/painter.svg";
import Search from "../img/search.svg";
import Footer from "./footer";

const TheoryPage = () => {
  return (
    <div className="theory">
      <Header />
      <main>
        <h1>Семантика</h1>
        <section>
          <details>
            <summary style={{ display: "block" }}>
              <h2 style={{ position: "relative" }}>
                Що це?
                <i
                  style={{
                    position: "absolute",
                    right: "10px",
                    color: "#024059",
                  }}
                  class="fas fa-angle-down"
                ></i>
              </h2>
            </summary>
            <div className="text-content">
              <p>
                Давним-давно майже всі робили сайти і не переймалися тим, що під
                капотом. Верстали таблицями, використовували все, що попадеться
                під руку (а траплялися в основному div і span ) і не особливо
                морочились над доступністю. А потім з'явився HTML5 і помчало.
              </p>
              <p>
                Семантична верстка - підхід до розмітки, який спирається не на
                зміст сайту, а на значення кожного блоку і логічну структуру
                документа.
              </p>
              <img alt="" src={Painter} />
            </div>
          </details>
        </section>
        <section>
          <details>
            <summary style={{ display: "block" }}>
              <h2 style={{ position: "relative" }}>
                Навіщо вона потрібна?
                <i
                  style={{
                    position: "absolute",
                    right: "10px",
                    color: "#024059",
                  }}
                  class="fas fa-angle-down"
                ></i>
              </h2>
            </summary>
            <div className="text-content">
              <p>
                <h3>Щоб зробити веб-сайт доступним.</h3> Зрячі користувачі
                можуть без проблем з першого погляду зрозуміти, де частина
                сторінки знаходиться. Для незрячих чи частково незрячих все
                складніше. Основний інструмент для перегляду сайтів не браузер,
                який малює сторінку, а скринрідер, який читає текст зі сторінки
                вголос.
              </p>
              <p>
                Цей інструмент «зачитує» вміст сторінки, і семантична структура
                допомагає йому краще визначати, який блок, а користувачеві
                розуміти, про що йдеться. Таким чином, семантична розмітка
                допомагає більшій кількості користувачів працювати з вашим
                сайтом. Наприклад, наявність заголовків допомагає незрячим у
                навігації по сторінці. У скринридерів є функція навігації за
                заголовками, що прискорює знайомство з інформацією на сайті.
              </p>
              <p>
                <h3>Щоб сайт був вищим у пошукових системах.</h3> Пошуковики не
                розголошують правила ранжирування, але відомо, що наявність
                семантичної розмітки сторінок допомагає пошуковим роботам краще
                розуміти, що знаходиться на сторінці, і в залежності від цього
                ранжувати сайти в пошуковій видачі.
              </p>
              <p>
                <h3>Семантика прописана у стандартах.</h3> Багато розробників
                по-старому користуються конструкціями типу &lt;div id="nav"&gt;
                для позначення навігації або інших структурних елементів
                сторінки. Тим часом у стандарті HTML є кілька{" "}
                <Link
                  style={{ textDecoration: "none", color: "#8c503a" }}
                  to="/tagsPage"
                >
                  семантичних тегів
                </Link>
                , які рекомендується використовувати для розмітки сторінок
                замість &lt;div&gt; та &lt;span&gt;. У специфікації кожного
                семантичного елемента описана його роль.
              </p>
              <img alt="" src={Speakers} />
            </div>
          </details>
        </section>
        <section>
          <details>
            <summary style={{ display: "block" }}>
              <h2 style={{ position: "relative" }}>
                Семантична верстка
                <i
                  style={{
                    position: "absolute",
                    right: "10px",
                    color: "#024059",
                  }}
                  class="fas fa-angle-down"
                ></i>
              </h2>
            </summary>
            <div className="text-content">
              <p>
                HTML як мова семантичної розмітки страждала від фундаментального
                дефекту — його семантика фіксована, а не розширювана. Це не
                просто теоретична проблема. Розробники використовували class і
                id атрибути HTML для створення більш насиченої семантичної
                розмітки. Вонм завжди використувалися спеціальними словниками —
                тобто значеннями, які вони створили, а не ті ,що взяті з
                існуючих схем. У кращому випадку це псевдосемантична розмітка.
              </p>
              <p>
                Багато сторінок в Інтернеті використовували мікроформати, щоб
                додати більше структурованої семантики, ніж доступна в
                збідненому наборі елементів і атрибутів HTML . У цьому випадку
                значення, що використовуються для class атрибута, походять із
                узгоджених словників, іноді із нещодавно створених словників, де
                не існує твердого попереднього стандарту.
              </p>
              <h3>Розширювана семантика</h3>
              <p>
                {" "}
                Тут є цілком реальна проблема, яку потрібно вирішити. Потрібні
                механізми в HTML, які чітко й однозначно дають змогу розробникам
                додавати багатшу й змістовнішу семантику, а не псевдосемантику,
                до своєї розмітки. Це, мабуть, найголовніша мета для проекту
                HTML 5.
              </p>
              <h3>Отже, як HTML 5 вирішує цю проблему?</h3>{" "}
              <p>
                HTML 5 вводить ряд нових елементів:
                <br />
                <code>
                  &lt;article&gt;, &lt;aside&gt;, &lt;audio&gt;, &lt;bdi&gt;,
                  &lt;canvas&gt;, &lt;data&gt;, &lt;datalist&gt;,
                  &lt;details&gt;, &lt;figcaption&gt;, &lt;dialog&gt;,
                  &lt;embed&gt;, &lt;figcaption&gt;, &lt;figure&gt;,
                  &lt;footer&gt;, &lt;header&gt;, &lt;hgroup&gt;,
                  &lt;keygen&gt;, &lt;main&gt;, &lt;mark&gt;, &lt;menuitem&gt;,
                  &lt;meter&gt;, &lt;nav&gt;, &lt;output&gt;, &lt;picture&gt;,
                  &lt;progress&gt;, &lt;rp&gt;, &lt;rt&gt;, &lt;ruby&gt;,
                  &lt;section&gt;, &lt;source&gt;, &lt;summary&gt;, &lt;svg&gt;,
                  &lt;template&gt;, &lt;time&gt;, &lt;track&gt;, &lt;video&gt;,
                  &lt;wbr&gt;.
                </code>{" "}
                <br />
              </p>
              <p>
                Але на жаль, на сьогоднішній день розробники досі не можуть
                отримати 100% семантично-валідної верстки. В основному правильно
                використовують &lt;footer&gt;, &lt;header&gt;, &lt;nav&gt;,
                рідше або неправильно використовують &lt;aside&gt; &lt;main&gt;
                &lt;section&gt;. Інші елементи використовують рідко або взагалі
                не використовують. Тому правильна верстка допускає утримання як
                семантичних тегів, і блокових елементів (наприклад &lt;div&gt;).
                Потрібно знати та розуміти призначення тегів для їх правильного
                використання.
              </p>
              <img alt="" src={Search} />
            </div>
          </details>
        </section>
        <section>
          <details>
            <summary style={{ display: "block" }}>
              <h2 style={{ position: "relative" }}>
                Семантичні теги
                <i
                  style={{
                    position: "absolute",
                    right: "10px",
                    color: "#024059",
                  }}
                  class="fas fa-angle-down"
                ></i>
              </h2>
            </summary>
            <div className="text-content">
              <p>
                Відповідно до специфікації HTML5 кожен елемент належить до
                певної категорії:{" "}
              </p>
              <ul>
                <li>
                  <Mark />
                  Мета вміст
                </li>
                <li>
                  <Mark />
                  Потоковий вміст
                </li>
                <li>
                  <Mark />
                  Секційний вміст
                </li>
                <li>
                  <Mark />
                  Заголовний вміст
                </li>
                <li>
                  <Mark />
                  Текстовий вміст
                </li>
                <li>
                  <Mark />
                  Вбудований вміст
                </li>
                <li>
                  <Mark />
                  Інтерактивний вміст
                </li>

                <li>
                  <Mark />
                  <Link
                    style={{
                      textDecoration: "none",
                      color: "#8c503a",

                      fontWeight: "800",
                      borderRadius: "10px",
                      marginTop: "20px",
                      alignSelf: "center",
                    }}
                    to="/tagsPage"
                  >
                    Дізнатися більше про теги
                  </Link>
                </li>
              </ul>
            </div>
          </details>
        </section>
      </main>
      <Footer />
    </div>
  );
};

export default TheoryPage;
